import React from 'react';
import Instructor from './Instructor';
import useWindowDimensions from '../WindowDimension/useWindowDimensions';

export default function CourseInstructors({instructorarray}) {

    const { width } = useWindowDimensions();

    function showCards_3(){
        let cards_to_show_array = [];
        let remainder = instructorarray.length%3;
        let times = parseInt(instructorarray.length/3);
        let count = 0;
        for (let i=0; i<instructorarray.length; i++) {
            if(count<times){
                let cardcontent = 
                <div className="course-instructors-container">
                    <Instructor 
                        instructorimage={instructorarray[i].instructorimage}
                        instructorname={instructorarray[i].instructorname}
                        instructordescription={instructorarray[i].instructordescription}
                        instructorprofile={instructorarray[i].instructorprofile}
                    />

                    <Instructor 
                        instructorimage={instructorarray[i+1].instructorimage}
                        instructorname={instructorarray[i+1].instructorname}
                        instructordescription={instructorarray[i+1].instructordescription}
                        instructorprofile={instructorarray[i+1].instructorprofile}
                    />

                    <Instructor 
                        instructorimage={instructorarray[i+2].instructorimage}
                        instructorname={instructorarray[i+2].instructorname}
                        instructordescription={instructorarray[i+2].instructordescription}
                        instructorprofile={instructorarray[i+2].instructorprofile}
                    />
                </div>

                cards_to_show_array.push(cardcontent);
                let whitespace = <div>
                    <br/>
                    <br/>
                    <br/>
                    <br/>
                </div>
                cards_to_show_array.push(whitespace);

                i += 2;
                count += 1;
            }
            else{
                let remaining = instructorarray.slice(3*times);
                const blankcard = <div className="course-instructor" />;
                let blankinstructors = [];
                for(let j = 0; j < (3 - remainder); j++){
                    blankinstructors.push(blankcard);
                }
                let content = 
                <div className="course-instructors-container">
                    {remaining.map((el) => {
                        return(
                            <Instructor
                                instructorimage={el.instructorimage}
                                instructorname={el.instructorname}
                                instructordescription={el.instructordescription}
                                instructorprofile={el.instructorprofile}
                            />
                        );
                    })}
                    {blankinstructors.map(el => el)}
                </div>
                cards_to_show_array.push(content);
                i += 2;
            }
        }
        return cards_to_show_array;
    }

    function showCards_2(){
        let cards_to_show_array = [];
        let remainder = instructorarray.length%2;
        let times = parseInt(instructorarray.length/2);
        let count = 0;
        for (let i=0; i<instructorarray.length; i++) {
            if(count<times){
                let cardcontent = 
                <div className="course-instructors-container">
                    <Instructor 
                        instructorimage={instructorarray[i].instructorimage}
                        instructorname={instructorarray[i].instructorname}
                        instructordescription={instructorarray[i].instructordescription}
                        instructorprofile={instructorarray[i].instructorprofile}
                    />

                    <Instructor 
                        instructorimage={instructorarray[i+1].instructorimage}
                        instructorname={instructorarray[i+1].instructorname}
                        instructordescription={instructorarray[i+1].instructordescription}
                        instructorprofile={instructorarray[i+1].instructorprofile}
                    />
                </div>

                cards_to_show_array.push(cardcontent);
                let whitespace = <div>
                    <br/>
                    <br/>
                    <br/>
                    <br/>
                </div>
                cards_to_show_array.push(whitespace);

                i++;
                count += 1;
            }
            else{
                let remaining = instructorarray.slice(2*times);
                const blankcard = <div className="course-instructor" />;
                let blankinstructors = [];
                for(let j = 0; j < (2 - remainder); j++){
                    blankinstructors.push(blankcard);
                }
                let content = 
                <div className="course-instructors-container">
                    {remaining.map((el) => {
                        return(
                            <Instructor 
                                instructorimage={el.instructorimage}
                                instructorname={el.instructorname}
                                instructordescription={el.instructordescription}
                                instructorprofile={el.instructorprofile}
                            />
                        );
                    })}
                    {blankinstructors.map(el => el)}
                </div>
                cards_to_show_array.push(content);
                i++;
            }
        }
        return cards_to_show_array;
    }

    if(width<1200){
        return(
            <div>
                {showCards_2().map(cardcontent => cardcontent)}
            </div>
        );
    }

    return(
        <div>
            {showCards_3().map(cardcontent => cardcontent)}
        </div>
    );
}