import React,{useState} from 'react';
import skillshublogo from './SkillsHub.png';
import sbtetlogo from './sbtet_logo.jpg';
import './Appbar.css';
import { Link } from "react-router-dom";
import { HashLink } from 'react-router-hash-link';
import useWindowDimensions from '../WindowDimension/useWindowDimensions';
// import { StyledEngineProvider } from '@mui/material/styles';
import Drawer from "@mui/material/Drawer";
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Stack from '@mui/material/Stack';

export default function Appbar(){

    const { width } = useWindowDimensions();

    const [open, setOpen] = useState(false);

    if(width<900) {
        return(
            <div>
                <div className="appbar-menuicon-container">
                    <IconButton 
                        onClick={() => setOpen(true)}
                    >
                        <MenuIcon fontSize="large" sx={{color: 'white'}}/>
                    </IconButton>
                </div>
                <div className="Appbar">
                    <img src={skillshublogo} alt="skills_hub_logo" className="appbar-logo1"/>
                    <img src={sbtetlogo} alt="sbtet_logo" className="appbar-logo2"/>
                </div>
                {/* <StyledEngineProvider injectFirst> */}
                    <Drawer
                        anchor="right"
                        open={open}
                        onClose={() => setOpen(false)}
                    >
                        <div style={{width:'11em', marginLeft: '15px', marginTop: '60px', paddingBottom: '50px'}} >

                            <Stack spacing={3}>

                                <Link to="/" 
                                    style={{
                                        textDecoration: 'none'
                                    }}
                                    className="nav-link"
                                    onClick={() => setOpen(false)}
                                >
                                    Home
                                </Link>

                                <HashLink smooth to="/#programs" className="nav-link" onClick={() => setOpen(false)} style={{textDecoration: 'none'}}>
                                    Programmes
                                </HashLink>

                                <a href="https://sbtetimages.s3.ap-south-1.amazonaws.com/Policy%2BDocument.pdf" className="nav-link" >
                                    Programme Policy
                                </a>

                                <a href="https://sbtetimages.s3.ap-south-1.amazonaws.com/Brochure.pdf" className="nav-link" >
                                    Brochure
                                </a>
                                
                                <a href="https://forms.isb.edu/SkillsHub" className="nav-link" target="_blank">
                                    Nodal Officers
                                </a>

                                <a href="https://isb.au1.qualtrics.com/jfe/form/SV_0Jv6MikIhfDCUYu" className="nav-link" target="_blank">
                                    Enroll Now
                                </a>


                            </Stack>
                        </div>
                    </Drawer>
                {/* </StyledEngineProvider> */}
            </div>
        );
    }

    return(
        <div>
            <div className="page-title"></div>
            <div className="Appbar">
                <div>
                    <img src={skillshublogo} alt="skills_hub_logo" className="appbar-logo1"/>
                </div>

                <div className="appbar-navigation">
                    <Link to="/" 
                        style={{
                            textDecoration: 'none'
                        }}
                        className="nav-link"
                    >
                        Home
                    </Link>

                    <HashLink smooth to="/#programs" className="nav-link" style={{textDecoration: 'none'}}>
                        Programmes
                    </HashLink>

                    <a href="https://sbtetimages.s3.ap-south-1.amazonaws.com/Policy%2BDocument.pdf" className="nav-link" >
                        Programme Policy
                    </a>

                    <a href="https://sbtetimages.s3.ap-south-1.amazonaws.com/Brochure.pdf" className="nav-link" >
                        Brochure
                    </a>
                    
                    <a href="https://forms.isb.edu/SkillsHub" className="nav-link" target="_blank">
                        Nodal Officers
                    </a>

                    <a href="https://isb.au1.qualtrics.com/jfe/form/SV_0Jv6MikIhfDCUYu" className="nav-link" target="_blank">
                        Enroll Now
                    </a>
                </div>
                <div>
                    <img src={sbtetlogo} alt="sbtet_logo" className="appbar-logo2"/>
                </div>
            </div>
        </div>

    );
}